import axios from "axios";
import React, { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { BsPencilFill, BsSaveFill } from "react-icons/bs";
import { FaSearchPlus } from "react-icons/fa";
import { Link } from "react-router-dom";
import tokenHeader from "../authorization/tokenHeader";
import { ContentsCard } from "../components/Prediction";
import Modal  from "../components/Modal";
import Banner from "react-js-banner";

// TODO:    
//  ( ,   ) 
export const Profile = ({
  user,
  onSubmit,
  onToggle,
  setOnToggle,
  onChange,
}) => {
  return (
    <div>
      <p>
        {/*  input  */}
        <label className="input-file-button" htmlFor="input-file">
          <Img src={user.image.file} alt="#" disabled={onToggle} />
        </label>
        <input
          id="input-file"
          type="file"
          accept="image/*"
          onChange={onChange}
          disabled={onToggle}
          style={{ display: "none" }}
        />
      </p>
      {onToggle ? (
        <button
          title=""
          style={{ cursor: "pointer" }}
          onClick={() => setOnToggle(!onToggle)}
        >
          <BsPencilFill />
        </button>
      ) : (
        <button
          title=""
          style={{ cursor: "pointer" }}
          onClick={() => {
            setOnToggle(!onToggle);
            onSubmit();
          }}
        >
          <BsSaveFill />
        </button>
      )}
      <h3>{user.nickname}</h3>
    </div>
  );
};

//       
export const FriendProfile = ({ friend }) => {
  return (
    <ProfileContainer>
      <div>
        <Img
          src={`${process.env.REACT_APP_BASE_URL}/${friend.photolink}`}
          alt=""
        />
        <h1>{friend.nickname}</h1>
      </div>
    </ProfileContainer>
  );
};

const MyPage = ({
  user,
  friendList,
  onUserProfile,
  onRequestFriends,
}) => {
  const [onToggle, setOnToggle] = useState(true);
  const [friendNickname, setFriendNickname] = useState("");
  const [existence, setExistence] = useState(false);
  const [friend, setFriend] = useState("");
  const [quit, setQuit] = useState(false);
  const [recommendList, setRecommendList] = useState("");

  //     
  const fetchData = async () => {
    try {
      const response = await axios.get(`/api/mypage`, {
        headers: tokenHeader(),
      });
      onUserProfile({
        ...user,
        nickname: response.data.nickname,
        image: {
          file: `${process.env.REACT_APP_BASE_URL}/${response.data.photolink}`,
        },
      });
    } catch (error) {
      console.log(error.response);
    }
  };

  //       
  const fetchFriend = async () => {
    try {
      const response = await axios.get(`/api/mypage/list/friend`, {
        headers: tokenHeader(),
      });
      onRequestFriends(response.data);
    } catch (error) {
      console.log(error.response);
    }
  };

  const recommend = async () => {
    try {
      const response = await axios.get(`/api/mypage/recommend`, {
        headers: tokenHeader(),
      });
      setRecommendList(response.data);
    } catch (error) {
      console.log(error.response);
    }
  };
  useEffect(() => {
    fetchData();
    fetchFriend();
    recommend();
  }, []);

  //       URL ,    
  const onChange = (event) => {
    onUserProfile({
      ...user,
      image: {
        file: URL.createObjectURL(event.target.files[0]),
        files: event.target.files,
      },
    });
  };

  //       
  const onSubmit = async () => {
    if (user.image.files) {
      const imageFile = Array.from(user.image.files);
      const formData = new FormData();
      formData.append("file", imageFile[0]);
      try {
        const response = await axios.patch(
          `/api/mypage/modify/photo`,
          formData,
          {
            headers: tokenHeader(),
          }
        );
        console.log(response.data);
      } catch (error) {
        console.log(error.response);
      }
    }
  };

  //       ->   
  const onNicknameSubmit = async (e) => {
    e.preventDefault();
    setExistence(true);
    const body = {
      nickname: friendNickname,
    };
    if (friendNickname) {
      try {
        const response = await axios.post(`/api/mypage/find/friend`, body, {
          headers: tokenHeader(),
        });
        console.log("response", response);
        if (response.data.result !== "fail") {
          setExistence(false);
          setFriend(response.data);
          console.log(friend);
        } else {
          setExistence(true);
          setFriend("");
          setTimeout(() => {
            setExistence(false);
          }, 2000);
        }
      } catch (error) {
        console.log(error.response);
        setExistence(true);
        setFriend("");
        setTimeout(() => {
          setExistence(false);
        }, 2000);
      }
    }
  };

  //   OK               
  const handleOk = async () => {
    const body = {
      nickname: friend.nickname,
    };
    try {
      const response = await axios.post(`/api/mypage/add/friend`, body, {
        headers: tokenHeader(),
      });
      console.log(response.data);
      if (response.data.result === "fail") {
        alert("    .");
      }
      const fetchData = await axios.get(`/api/mypage/list/friend`, {
        headers: tokenHeader(),
      });
      onRequestFriends(fetchData.data);
      setFriendNickname("");
    } catch (error) {
      console.log(error.response);
    }
  };

  return (
    <div>
      <Banner
        showBanner={quit}
        css={{
          backgroundColor: "#0080ff",
          fontSize: 20,
          fontWeight: "lighter",
          color: "white",
          margin: "1rem auto",
          borderRadius: "25px",
        }}
        title="  .  ."
      />

      <Container>
        <ProfileContainer>
          <Profile
            user={user}
            onSubmit={onSubmit}
            onToggle={onToggle}
            setOnToggle={setOnToggle}
            onChange={onChange}
          />
        </ProfileContainer>
        <div style={{ height: "350px", background: "blue", maxWidth: "800px" }}>
          <h1> </h1>
          {!recommendList ? (
            <span>   .</span>
          ) : (
            recommendList.map((recommend, idx) => (
              <Link
                to={`/detail/${recommend.category}/${recommend.id}/${recommend.title}`}
              >
                <ContentsCard contents={recommend} key={idx} />
              </Link>
            ))
          )}
        </div>
        <div style={{ width: "250px", background: "pink", margin: "0 auto" }}>
          <Modal setQuit={setQuit} />
        </div>
        <div style={{ background: "red", maxWidth: "800px" }}>
          <div>
            <form onSubmit={onNicknameSubmit}>
              <span> </span>
              <input
                type="text"
                value={friendNickname}
                onChange={(e) => setFriendNickname(e.target.value)}
                placeholder="  ."
              />
              <button type="submit" shape="circle">
                <FaSearchPlus />
              </button>
            </form>
            {existence && <h1>  .</h1>}
            {!friend ? (
              <span></span>
            ) : (
              <ProfileContainer>
                <div>
                  <Img
                    src={`${process.env.REACT_APP_BASE_URL}/${friend.photolink}`}
                    alt=""
                  />
                  <h1>{friend.nickname}</h1>
                  <button onClick={handleOk}></button>
                </div>
              </ProfileContainer>
            )}
          </div>
          <div> </div>
          <div style={{ background: "green", height: "250px" }}>
            {!friendList ? (
              <div></div>
            ) : (
              friendList.map((friend) => (
                <Link to={`/potato-basket/${friend.nickname}`}>
                  <FriendProfile key={friend.nickname} friend={friend} />
                </Link>
              ))
            )}
          </div>
        </div>
      </Container>
    </div>
  );
};

export default MyPage;

//TODO: styled-components     
const Container = styled.div`
  margin: 30px auto;
  max-width: 1400px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 20px;
  justify-self: center;
`;

const ProfileContainer = styled.div`
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  background-color: white;
  margin: 0 auto;
  width: 250px;
  height: 350px;
  border-radius: 15px;
  box-shadow: 0 6px 12px -2px rgba(50, 50, 93, 0.25),
    0 3px 7px -3px rgba(0, 0, 0, 0.3);
`;

const Img = styled.img`
  width: 125px;
  height: 125px;
  border-radius: 50%;
  border: none;
  background-color: #f2f2f2;
  box-shadow: 0 6px 12px -2px rgba(50, 50, 93, 0.25),
    0 3px 7px -3px rgba(0, 0, 0, 0.3);
  ${(props) => {
    if (!props.disabled) {
      return css`
        cursor: pointer;
      `;
    }
  }}
`;
